@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: #ffffff;
  margin: 0 auto;
  color: white;
  line-height: 1.6;
  font: 400 15px/28px "Poppins", sans-serif!important;
  overflow-x: hidden!important;
}


/* Global */
button {
  background-color: #ff9a41!important;
  color: #323982!important;
  font-size: 1rem!important;
  padding: 0.8rem 1.5rem!important;
  border-radius: 13px 13px 13px 0px !important;
  font-weight: 600!important;
}

h1, h2, h3, h4, h5{
  font-weight: 600!important;
}
/* Global */

nav {
  background-color: #323982b3;
}
nav a {
  padding-right: 1rem!important;
  padding-left: 1rem!important;

}
.navbar-dark .navbar-nav .show > .nav-link, .navbar-dark .navbar-nav .active > .nav-link, .navbar-dark .navbar-nav .nav-link.show, .navbar-dark .navbar-nav .nav-link.active {
 background-color: #08040427;
}

nav button {
 
  text-transform: capitalize!important;
  font-size: 1rem!important;
  padding: 0.8rem 1.3rem!important;
  
}



/* Showcase */
.showcase {
  margin: 0;
 padding: 0;
 background:linear-gradient( #171d59bd,#171d59bd),url(/static/media/Picture2.f52178a8.png);
 background-size: cover;
 width: 100vw;
 height: 80vh;
 position: relative;
 overflow-y: hidden;
}


.showcase .showcase-content {
  float: left!important;
  color: #ffffff;
  
  margin-top: 25vh;
  text-align: justify;
}

/* Showcase */


/* Service */

.services {
  color: #323982 ;
  
}

.services h3 {
  font-size: 2rem;
}

.services .card{
  box-shadow: none;
  transition: all 0.2s linear;
}

.services .card-title {
font-weight: 500;
}

.services .card-text {
  margin-top: 1rem;
  color: #323982!important;
   text-align: justify;

}

@media (prefers-reduced-motion: no-preference) {
.services img  {
   -webkit-animation: App-logo-spin infinite 5s linear;
           animation: App-logo-spin infinite 5s linear;
 }
}

@-webkit-keyframes App-logo-spin {
 from {
   transform: rotate(0deg);
 }
 to {
   transform: rotate(360deg);
 }
}

@keyframes App-logo-spin {
 from {
   transform: rotate(0deg);
 }
 to {
   transform: rotate(360deg);
 }
}
/* Service */


/* Partner */

.partners h3{
  color: #323982!important;
  font-size: 1.3rem!important;
  
}
