/* Team */
.team {
  text-align: center;
  color: #323982;
}

.team .card {
  box-shadow: none;
}

.team img {
  border-radius: 30px;
  width: 150px;
}

.team h3{
  color: #323982!important;
  font-size: 1.3rem!important;
  text-align: left;
}

.team .card-text {
  margin-top: 0.5rem;
  color: #323982!important;
  font-size: 0.7rem!important;
 }
