/* Who We Are */

.whoweare .row-text {
  background-color: #171c58bd ;
  padding-right: 3rem;
  color: #ffffff;
}
.whoweare .row-text{
 padding: 5rem 3rem!important;
}

.whoweare .row-text h4 {
  color: #ff9a41;
}

.whoweare img {
