/* Enrol Formm */
.enrolform {
  background-color: #ffffff;
  padding: 2rem 0;
  margin-top: 5rem;
}

.enrolform .container {
  background-color: #ff9a41a9;
  padding: 2rem;
}
 
.enrolform input {
  
  border: 1px solid #323982;
}
.enrolform textarea {
  
  border: 1px solid #323982!important;
  border-radius: 5px;
}


.enrolform input::-webkit-input-placeholder {
  
  color: #323982;
}


.enrolform input:-ms-input-placeholder {
  
  color: #323982;
}


.enrolform input::-ms-input-placeholder {
  
  color: #323982;
}


.enrolform input::placeholder {
  
  color: #323982;
}

.enrolform textarea::-webkit-input-placeholder {
  
  color: #323982;
}

.enrolform textarea:-ms-input-placeholder {
  
  color: #323982;
}

.enrolform textarea::-ms-input-placeholder {
  
  color: #323982;
}

.enrolform textarea::placeholder {
  
  color: #323982;
}

.enrolform h4{
  color: #323982;
}

.enrolform button{
  background-color: #323982;
  color: #ffffff;
  border: 0;
}


