/* Contact US */
#contact {
  margin-top: 3rem;
}
#contact .btn-outline-info {
  border-color: #fff !important;
}

#contact a {
  color: #090b0f;
  font-size: 25px;
  padding-left: 2rem;
}

#contact .btn-outline-info {
  color: #fff !important;
  font-size: 15px;
  
}

.contact input, .contact textarea {
  
  color: #ffffff!important;
}

.contact input::-webkit-input-placeholder, .contact textarea::-webkit-input-placeholder {
  
  color: #ffffff;
}

.contact input:-ms-input-placeholder, .contact textarea:-ms-input-placeholder {
  
  color: #ffffff;
}

.contact input::-ms-input-placeholder, .contact textarea::-ms-input-placeholder {
  
  color: #ffffff;
}

.contact input::placeholder, .contact textarea::placeholder {
  
  color: #ffffff;
}


.contact { 
  background-repeat: no-repeat;
  background-image:linear-gradient( rgba(23, 29, 89, 0.741),rgba(23, 29, 89, 0.741)),url(/static/media/Picture2.f52178a8.png);
  background-size: cover;
 padding: 60px 60px;
 height: 150vh;
}


.contact .card {
  background-color: transparent!important;
  color: #ffffff;
  border: 1px solid white;
  border-radius: 0!important;
}

.contact hr {
  border-top: 0.1px solid #ffffffd3;
}

.socials .box-icon {
  padding: 0.6rem;
  text-align: center !important;
  background-color: #2c1603;
  border-radius: 50%;
  box-shadow: 2px 0px 30px #978e8e;
}


